// @ts-nocheck
import React, { useState, useCallback, useRef } from "react";
import {
  Page,
  Layout,
  Card,
  FormLayout,
  TextField,
  Badge,
  Thumbnail,
  Avatar,
  PageActions,
  ResourceList,
  TextStyle,
  Stack,
  TextContainer,
  DisplayText,
  MediaCard,
  VideoThumbnail,
  Checkbox,
  ContextualSaveBar,
  Loading,
  SkeletonPage,
  SkeletonDisplayText,
  SkeletonBodyText,
  ResourceItem,
  DataTable,
  Icon,
} from "@shopify/polaris";
import { ExportMinor, NoteMinor } from "@shopify/polaris-icons";
import { gql } from "apollo-boost";
import { useQuery, useMutation } from "@apollo/react-hooks";

import { UserAvatar, NotificationListAvatar } from '../lib/imageUtils';

const QUERY_SETTINGS = gql`
  {
    settingsItems(filter: { category: { eq: "website" } }) {
      edges {
        node {
          id
          json
        }
      }
    }
  }
`;

const UPDATE_SETTINGS = gql`
  mutation UpdateSettings($id: ID!, $json: String!) {
    updateOneSettingsItem(input: { id: $id, update: { json: $json } }) {
      id
      json
    }
  }
`;

export default function SettingsNotificationListPage() {
  const { loading, error, data } = useQuery(QUERY_SETTINGS);

  if (loading)
    return (
      <SkeletonPage title="Preferences">
        <Loading />
        <Layout>
          <Layout.Section>
            <Card sectioned>
              <TextContainer>
                <SkeletonDisplayText size="small" />
                <SkeletonBodyText lines={9} />
              </TextContainer>
            </Card>
          </Layout.Section>
        </Layout>
      </SkeletonPage>
    );
  // TODO - removed so we can have a preview of the page
  // if (error)
  //   return (
  //     <Page title="Preferences">
  //       <Layout>
  //         <Layout.Section>
  //           <p>Ops, something went wrong. Refresh to try again.</p>
  //         </Layout.Section>
  //       </Layout>
  //     </Page>
  //   );

  const settingsRecord = data && data.settingsItems.edges.length
    ? data.settingsItems.edges[0].node
    : {};
  return <SettingsNotificationListPageChild settingsRecord={settingsRecord} />;
}

function checkField(key, settings, savedSettings) {
  if (!savedSettings[key] && !settings[key]) return false;
  return savedSettings[key] !== settings[key];
}

function renderItem(item) {
  const {
    id,
    name,
    email,
    lastLogin,
    role,
  } = item;
  const media = <UserAvatar customer size="medium" name={id} email={email} />;
  const shortcutActions = null;

  return (
    <ResourceItem
      id={id}
      url={`/users/${id}`}
      media={media}
      accessibilityLabel={`View details for ${email}`}
      // shortcutActions={shortcutActions}
      // persistActions
    >
      <div style={{ display: 'flex', justifyContent: 'space-between' }}>
        <div style={{ width: '80%' }}>
          <h3>
            <TextStyle variation="strong">{name}</TextStyle>
          </h3>
          <div>Last login: {lastLogin}</div>
        </div>

        <div style={{ width: '20%' }}>
          {role}
        </div>

      </div>
    </ResourceItem>
  );
}

function SettingsNotificationListPageChild({ settingsRecord }) {
  const FIELDS = [
    // "name",  // read-only
    "title",
    "description",
    "domain",
    "domain_redirect",
    "google_analytics",
    "google_tag_manager",
    "password",
    "password_enabled",
    "password_message"
  ];
  let settingsData = {};
  try {
    settingsData = JSON.parse(settingsRecord.json);
  } catch (e) {
    // pass
  }

  const [updateSettings] = useMutation(UPDATE_SETTINGS);
  const savedSettings = useRef({
    ...settingsData
  });

  const [isDirty, setIsDirty] = useState(false);
  const [settings, setSettings] = useState(savedSettings.current);

  const handleDiscard = useCallback(() => {
    setSettings(savedSettings.current);
    setIsDirty(false);
  }, []);
  const handleSave = useCallback(() => {
    updateSettings({
      variables: { id: settingsRecord.id, json: JSON.stringify(settings) }
    });
    savedSettings.current = settings;
    setIsDirty(false);
  }, [settings]);

  const handleFieldChange = useCallback(
    (field, value) => {
      const newSettings = { ...settings };
      newSettings[field] = value;
      const isDirty = FIELDS.some(key =>
        checkField(key, newSettings, savedSettings.current)
      );
      setSettings(newSettings);
      setIsDirty(isDirty);
    },
    [settings]
  );

  const contextualSaveBarMarkup = isDirty ? (
    <ContextualSaveBar
      message="Unsaved changes"
      saveAction={{
        onAction: handleSave
      }}
      discardAction={{
        onAction: handleDiscard
      }}
    />
  ) : null;

  const rows = [
    ['Subscription', '', '$299.00'],
    ['Transaction fees', '', '$34.27'],
    ['', 'Subtotal', '$333.27'],
  ];

  return (
    <Page
      title={"Bill"}
      breadcrumbs={[{ content: "Billing", url: "/settings/bills" }]}
      secondaryActions={[
        { content: "Download", icon: () => <Icon source={NoteMinor} />, },
        { content: "Export as CSV", icon: () => <Icon source={ExportMinor} />, },
      ]}
    >
      {contextualSaveBarMarkup}
      <Layout>
        <Layout.Section
          title="Permissions"
          description="Manage what NotificationList can see or do in your admin dashboard."
        >
          <Card>
            <Card.Section>
              <strong>
              <Stack>
                <Stack.Item fill>Subscription</Stack.Item>
                <Stack.Item>$299.00</Stack.Item>
              </Stack>
              </strong>
            </Card.Section>
            <Card.Section subdued>
              <Stack>
                <Stack.Item fill>Pro plan (every 30 days)</Stack.Item>
                <Stack.Item>$299.00</Stack.Item>
              </Stack>
            </Card.Section>

            <Card.Section>
              <Card.Subsection>
                <Stack distribution="trailing">
                  <Stack.Item>Subtotal</Stack.Item>
                  <Stack.Item>$299.00</Stack.Item>
                </Stack>
              </Card.Subsection>
              <Card.Subsection>
                <strong>
                <Stack distribution="trailing">
                  <Stack.Item>Total</Stack.Item>
                  <Stack.Item>$299.00</Stack.Item>
                </Stack>
                </strong>
              </Card.Subsection>
            </Card.Section>
          </Card>
        </Layout.Section>
        <Layout.Section secondary>
          <Card>
            <div style={{ textAlign: 'center', padding: 20 }}>
              <DisplayText>$299.00</DisplayText>
            </div>
            <div style={{ backgroundColor: '#bbe5b3', textAlign: 'center', padding: 10 }}>
              <DisplayText>Paid</DisplayText>
            </div>

            <Card.Section title="Bill number">
              82687365
            </Card.Section>
            <Card.Section title="Bill paid">
              Jun 26, 2020
            </Card.Section>
          </Card>
        </Layout.Section>
      </Layout>
    </Page>
  );
}
