// @ts-nocheck
import React, { useState, useCallback, useRef } from "react";
import {
  Page,
  Layout,
  Card,
  FormLayout,
  TextField,
  Badge,
  Thumbnail,
  Avatar,
  PageActions,
  ResourceList,
  TextStyle,
  Stack,
  TextContainer,
  DisplayText,
  MediaCard,
  VideoThumbnail,
  Checkbox,
  ContextualSaveBar,
  Loading,
  SkeletonPage,
  SkeletonDisplayText,
  SkeletonBodyText,
  Select,
  Button,
  ChoiceList,
} from "@shopify/polaris";
import { InviteMinor } from "@shopify/polaris-icons";
import { gql } from "apollo-boost";
import { useQuery, useMutation } from "@apollo/react-hooks";

const QUERY_SETTINGS = gql`
  {
    settingsItems(filter: { category: { eq: "website" } }) {
      edges {
        node {
          id
          json
        }
      }
    }
  }
`;

const UPDATE_SETTINGS = gql`
  mutation UpdateSettings($id: ID!, $json: String!) {
    updateOneSettingsItem(input: { id: $id, update: { json: $json } }) {
      id
      json
    }
  }
`;

export default function SettingsCheckoutPage() {
  const { loading, error, data } = useQuery(QUERY_SETTINGS);

  if (loading)
    return (
      <SkeletonPage title="Preferences">
        <Loading />
        <Layout>
          <Layout.Section>
            <Card sectioned>
              <TextContainer>
                <SkeletonDisplayText size="small" />
                <SkeletonBodyText lines={9} />
              </TextContainer>
            </Card>
          </Layout.Section>
        </Layout>
      </SkeletonPage>
    );
  // TODO - removed so we can have a preview of the page
  // if (error)
  //   return (
  //     <Page title="Preferences">
  //       <Layout>
  //         <Layout.Section>
  //           <p>Ops, something went wrong. Refresh to try again.</p>
  //         </Layout.Section>
  //       </Layout>
  //     </Page>
  //   );

  const settingsRecord = data && data.settingsItems.edges.length
    ? data.settingsItems.edges[0].node
    : {};
  return <SettingsCheckoutPageChild settingsRecord={settingsRecord} />;
}

function checkField(key, settings, savedSettings) {
  if (!savedSettings[key] && !settings[key]) return false;
  return savedSettings[key] !== settings[key];
}

function SettingsCheckoutPageChild({ settingsRecord }) {
  const FIELDS = [
    // "name",  // read-only
    "title",
    "description",
    "domain",
    "domain_redirect",
    "google_analytics",
    "google_tag_manager",
    "password",
    "password_enabled",
    "password_message"
  ];
  let settingsData = {};
  try {
    settingsData = JSON.parse(settingsRecord.json);
  } catch (e) {
    // pass
  }

  const [updateSettings] = useMutation(UPDATE_SETTINGS);
  const savedSettings = useRef({
    ...settingsData
  });

  const [isDirty, setIsDirty] = useState(false);
  const [settings, setSettings] = useState(savedSettings.current);

  const handleDiscard = useCallback(() => {
    setSettings(savedSettings.current);
    setIsDirty(false);
  }, []);
  const handleSave = useCallback(() => {
    updateSettings({
      variables: { id: settingsRecord.id, json: JSON.stringify(settings) }
    });
    savedSettings.current = settings;
    setIsDirty(false);
  }, [settings]);

  const handleFieldChange = useCallback(
    (field, value) => {
      const newSettings = { ...settings };
      newSettings[field] = value;
      const isDirty = FIELDS.some(key =>
        checkField(key, newSettings, savedSettings.current)
      );
      setSettings(newSettings);
      setIsDirty(isDirty);
    },
    [settings]
  );

  const contextualSaveBarMarkup = isDirty ? (
    <ContextualSaveBar
      message="Unsaved changes"
      saveAction={{
        onAction: handleSave
      }}
      discardAction={{
        onAction: handleDiscard
      }}
    />
  ) : null;

  const industryOptions = [
    {label: 'Finance', value: 'finance'},
    {label: 'Internet', value: 'internet'},
    {label: 'Security', value: 'security'},
  ];

  return (
    <Page
      title={"Signup and checkout"}
      breadcrumbs={[{ content: "Settings", url: "/settings" }]}
    >
      {contextualSaveBarMarkup}
      <Layout>
        <Layout.AnnotatedSection
          title="Signup"
          description="Customize the signup experience, including billing and email verification steps."
        >
          <Card>
            <Card.Section title="Billing during signup">
              <ChoiceList
                title=""
                choices={[
                  {
                    label: 'Hidden (default)',
                    value: 'hidden',
                    helpText: 'Don\'t ask for credit card info during signup (if free plan or free trial available)',
                  },
                  {
                    label: 'Optional',
                    value: 'optional',
                    helpText: 'Ask for credit card info during signup, but let user skip (if free plan or free trial available)',
                  },
                  {
                    label: 'Required',
                    value: 'required',
                    helpText: 'Always require credit card info during signup, even for free plan or free trials.',
                  },
                ]}
                selected={['hidden']}
                onChange={() => []}
              />
            </Card.Section>
            <Card.Section title="Email verification">
              <ChoiceList
                title=""
                choices={[
                  {
                    label: 'Required',
                    value: 'required',
                    helpText: 'Require users to verify their email before their first login.',
                  },
                  {
                    label: 'Optional (default)',
                    value: 'optional',
                    helpText: 'Send users a verification email, but let them log in without requiring verification',
                  },
                  {
                    label: 'Disabled',
                    value: 'disabled',
                    helpText: 'Don\'t send any verification email',
                  },
                ]}
                selected={['optional']}
                onChange={() => []}
              />
            </Card.Section>
            <Card.Section title="Email verification from socials">
              <Checkbox
                label="Don't send verification email when already verified on socials (default: don't send)"
                onChange={value => handleFieldChange("industry", value)}
                checked={true}
                helpText={'When users sign up with, e.g., Google their email is automatically verified'}
              />
            </Card.Section>
          </Card>
        </Layout.AnnotatedSection>

        <Layout.AnnotatedSection
          title="Checkout form"
          description="Customize the checkout form that users will see to input their billing and credit card information."
        >
          <Card>
            <Card.Section>
              <ChoiceList
                title="Full name"
                choices={[
                  {
                    label: 'Require last name only (default)',
                    value: 'require_last',
                  },
                  {
                    label: 'Require first and last name',
                    value: 'require_full',
                  },
                ]}
                selected={['require_last']}
                onChange={() => []}
              />
            </Card.Section>
            <Card.Section>
              <ChoiceList
                title="Company name"
                choices={[
                  {
                    label: 'Hidden',
                    value: 'hidden',
                  },
                  {
                    label: 'Optional (default)',
                    value: 'optional',
                  },
                  {
                    label: 'Required',
                    value: 'required',
                  },
                ]}
                selected={['optional']}
                onChange={() => []}
              />
            </Card.Section>
            <Card.Section>
              <ChoiceList
                title="Address line 2 (apartment, unit, etc.)"
                choices={[
                  {
                    label: 'Hidden',
                    value: 'hidden',
                  },
                  {
                    label: 'Optional (default)',
                    value: 'optional',
                  },
                  {
                    label: 'Required',
                    value: 'required',
                  },
                ]}
                selected={['optional']}
                onChange={() => []}
              />
            </Card.Section>
            <Card.Section>
              <ChoiceList
                title="Phone number (it won't be verified)"
                choices={[
                  {
                    label: 'Hidden',
                    value: 'hidden',
                  },
                  {
                    label: 'Optional (default)',
                    value: 'optional',
                  },
                  {
                    label: 'Required',
                    value: 'required',
                  },
                ]}
                selected={['optional']}
                onChange={() => []}
              />
            </Card.Section>
          </Card>
        </Layout.AnnotatedSection>
        <Layout.Section>
          <PageActions
            primaryAction={{
              content: "Save",
              onAction: handleSave,
              disabled: !isDirty
            }}
          />
        </Layout.Section>
      </Layout>
    </Page>
  );
}
