// @ts-nocheck
import React, { useState, useCallback } from "react";
import { gql } from "apollo-boost";
import { useQuery, useMutation } from "@apollo/react-hooks";
import {
  Page,
  Layout,
  Card,
  FormLayout,
  TextField,
  ChoiceList,
  RangeSlider,
  ResourceList,
  Filters,
  Avatar,
  TextStyle,
  Button,
  ResourceItem,
  Icon,
  Stack,
  Badge,
  Tabs,
} from "@shopify/polaris";
import { ExportMinor, ImportMinor } from "@shopify/polaris-icons";

import { toCurrency, toShortDate } from "../lib/textUtils";

import { ALL_LEADS, DELETE_LEADS } from "../data/queries";
import { leads } from "../data/accounts";

let _queryValue = "";
let _pagingItems = 3;

function LeadsResourceList({
  data,
  queryValue,
  setQueryValue,
}) {
  const [selectedItems, setSelectedItems] = useState([]);
  const [taggedWith, setTaggedWith] = useState("");

  const handleQueryValueChange = useCallback(
    (value) => {
      _queryValue = value;
      return setQueryValue(value);
    },
    []
  );
  const handleTaggedWithRemove = useCallback(() => setTaggedWith(""), []);
  const handleQueryValueRemove = useCallback(() => setQueryValue(""), []);
  const handleClearAll = useCallback(() => {
    handleTaggedWithRemove();
    handleQueryValueRemove();
  }, [handleQueryValueRemove, handleTaggedWithRemove]);

  const [deleteLead] = useMutation(DELETE_LEADS);

  const resourceName = {
    singular: "lead",
    plural: "leads",
  };

  const promotedBulkActions = [
    {
      content: "Resend recovery email",
      onAction: () => {
        console.log("todo");
      },
      disabled: true
    },
  ];

  const bulkActions = [
    {
      content: "Delete",
      onAction: () => {
        const idFilter =
          selectedItems === "All"
            ? { gte: 0 } : { in: selectedItems };

        deleteLead({
          variables: {
            deleteManyLeadsInput: {
              filter: { id: idFilter},
            },
          },
          awaitRefetchQueries: true,
          refetchQueries: [{ query: ALL_LeadS }],
          update: (cache, data) => {
            // Horrible hack to reload the page.
            // I'm sorry. Fix this please.
            // A better way is to reload on the `onCompleted`
            // but there is a bug that prevents it from firing :-(
            // see here: https://github.com/apollographql/react-apollo/issues/2177
            window.location.reload()
          }
        });
      },
    },
  ];

  // TODO: add filters
  const filters = [
    // {
    //   key: "filterStatus",
    //   label: "Status",
    //   filter: (
    //     <ChoiceList
    //       title="Account status"
    //       titleHidden
    //       choices={[
    //         { label: "Email sent", value: "sent" },
    //         { label: "Email not sent", value: "notsent" },
    //         { label: "Recovered", value: "recovered" },
    //         { label: "Not recovered", value: "notrecovered" },
    //       ]}
    //       selected={filterStatus || []}
    //       onChange={handleFilterStatusChange}
    //       allowMultiple
    //     />
    //   ),
    //   shortcut: true,
    // },
  ];

  const appliedFilters = !isEmpty(taggedWith)
    ? [
        {
          key: "taggedWith",
          label: disambiguateLabel("taggedWith", taggedWith),
          onRemove: handleTaggedWithRemove,
        },
      ]
    : [];

  const filterControl = (
    <Filters
      queryValue={queryValue}
      filters={filters}
      appliedFilters={appliedFilters}
      onQueryChange={handleQueryValueChange}
      onQueryClear={handleQueryValueRemove}
      onClearAll={handleClearAll}
    />
  );

  if (!data) return "";

  return (
    <ResourceList
      resourceName={resourceName}
      items={data["leads"]["edges"]}
      idForItem={(lead)=>lead.node.id}
      renderItem={renderItem}
      selectedItems={selectedItems}
      onSelectionChange={setSelectedItems}
      promotedBulkActions={promotedBulkActions}
      bulkActions={bulkActions}
      hasMoreItems={data["leads"]["pageInfo"]["hasNextPage"]}
      filterControl={filterControl}
    />
  );

  function renderItem(item) {
    const { id, email, updated, created, json } = item["node"];

    let _json = null;
    try {
      _json = JSON.parse(json);
    } catch (err) {
      //console.log(err);
    }

    const {
      account_plan,
      account_price,
      account_freq,
      ltv,
      tags,
      email_status,
      lead_status,
    } = _json ? _json : {};

    const accountPrice = toCurrency(account_price);

    const leadStatusDisplay = {
      new: {
        color: 'new',
        label: "New",
      },
      converted: {
        color: "success",
        label: "Converted",
      },
      notconverted: {
        color: "warning",
        label: "Not converted",
      },
    };
    const leadStatus = leadStatusDisplay[lead_status] || {
      color: "attention",
      label: "Unkwnown",
    };

    // TODO: use enums or something like this
    const emailStatusDisplay = {
      sent: {
        color: "success",
        label: "Sent",
      },
      pending: {
        color: "warning",
        label: "Not sent",
      },
      scheduled: {
        color: "info",
        label: "Scheduled",
      },
      notsent: {
        color: "warning",
        label: "Not sent",
      },
    };
    const emailStatus = emailStatusDisplay[email_status] || {
      color: "attention",
      label: "Unkwnown",
    };

    const tagsDisplay = (tags || []).map((t) => <Badge>{t}</Badge>);

    return (
      <ResourceItem
        id={id}
        accessibilityLabel={`View details for ${email}`}
      >
        <div style={{ display: "flex", justifyContent: "space-between" }}>
          <div style={{ width: "15%" }}>{toShortDate(updated)}</div>

          <div style={{ width: "30%" }}>
            <h3>
              <TextStyle variation="strong">{email}</TextStyle>
            </h3>
          </div>

          {/* <div style={{ width: "30%" }}>
            <h3>
              <TextStyle variation="strong">{id}</TextStyle>
            </h3>
          </div> */}

          <div
            style={{ display: "flex", width: "20%", justifyContent: "center" }}
          >
            <Stack alignment="center" spacing="extraTight">
              <Badge status={emailStatus.color}>{emailStatus.label}</Badge>
            </Stack>
          </div>

          <div
            style={{ display: "flex", width: "20%", justifyContent: "center" }}
          >
            <Stack alignment="center" spacing="extraTight">
              <Badge status={leadStatus.color}>
                {leadStatus.label}
              </Badge>
            </Stack>
          </div>
        </div>
      </ResourceItem>
    );
  }

  function disambiguateLabel(key, value) {
    switch (key) {
      case "taggedWith":
        return `Tagged with ${value}`;
      default:
        return value;
    }
  }

  function isEmpty(value) {
    if (Array.isArray(value)) {
      return value.length === 0;
    } else {
      return value === "" || value == null;
    }
  }
}

const getLeadsVariables = () => {
  const queryVariable = _queryValue
    ? {
        LeadFilter: {
          email: { like: `%${_queryValue}%` },
        },
      }
    : {};

  const paginationVariable = {
    Paging: {
      first: _pagingItems,
    },
  };

  return { ...queryVariable, ...paginationVariable }
};

export default function LeadListPage() {
  const [queryValue, setQueryValue] = useState("");
  const [pagingItems, setPagingItems] = useState(3);

  let { loading, error, data } = useQuery(ALL_LEADS, {
    variables: getLeadsVariables(),
  });

  // TODO: do this better
  if (error) data = {
    leads: {
      edges: leads.map(x => ({node: {
        ...x,
        json: JSON.stringify({
          email_status: x.email_status,
          lead_status: x.lead_status,
        })
      }})),
      pageInfo: {
        hasNextPage: false,
      },
    },
  };

  return (
    <Page
      title="Leads"
      secondaryActions={[
        {
          icon: () => <Icon source={ExportMinor} />,
          content: "Export",
          url: "http://localhost:3000/leads/exportToCsv"
        },
      ]}
    >
      <Layout>
        <Layout.Section>
          <Card>
            <LeadsResourceList
              loading={loading}
              error={error}
              data={data}
              queryValue={queryValue}
              pagingItems={pagingItems}
              setQueryValue={setQueryValue}
            />
          </Card>
          {data && data["leads"]["pageInfo"]["hasNextPage"] && (
            <p onClick={() =>{
              _pagingItems += 5;
              return setPagingItems(pagingItems + 5);
            } }>Show more ...</p>
          )}
        </Layout.Section>
      </Layout>
    </Page>
  );
}
