// @ts-nocheck
import React, { useState, useCallback, useRef } from "react";
import {
  Page,
  Layout,
  Card,
  FormLayout,
  TextField,
  Badge,
  Thumbnail,
  Avatar,
  PageActions,
  ResourceList,
  TextStyle,
  Stack,
  TextContainer,
  DisplayText,
  MediaCard,
  VideoThumbnail,
  Checkbox,
  ContextualSaveBar,
  Loading,
  SkeletonPage,
  SkeletonDisplayText,
  SkeletonBodyText
} from "@shopify/polaris";
import { InviteMinor } from "@shopify/polaris-icons";
import { gql } from "apollo-boost";
import { useQuery, useMutation } from "@apollo/react-hooks";

const QUERY_SETTINGS = gql`
  {
    settingsItems(filter: { category: { eq: "website" } }) {
      edges {
        node {
          id
          json
        }
      }
    }
  }
`;

const UPDATE_SETTINGS = gql`
  mutation UpdateSettings($id: ID!, $json: String!) {
    updateOneSettingsItem(input: { id: $id, update: { json: $json } }) {
      id
      json
    }
  }
`;

export default function WebsitePreferencesPage() {
  const { loading, error, data } = useQuery(QUERY_SETTINGS);

  if (loading)
    return (
      <SkeletonPage title="Preferences">
        <Loading />
        <Layout>
          <Layout.Section>
            <Card sectioned>
              <TextContainer>
                <SkeletonDisplayText size="small" />
                <SkeletonBodyText lines={9} />
              </TextContainer>
            </Card>
          </Layout.Section>
        </Layout>
      </SkeletonPage>
    );
  // TODO - removed so we can have a preview of the page
  // if (error)
  //   return (
  //     <Page title="Preferences">
  //       <Layout>
  //         <Layout.Section>
  //           <p>Ops, something went wrong. Refresh to try again.</p>
  //         </Layout.Section>
  //       </Layout>
  //     </Page>
  //   );

  const settingsRecord = data && data.settingsItems.edges.length
    ? data.settingsItems.edges[0].node
    : {};
  return <WebsitePreferencesPageChild settingsRecord={settingsRecord} />;
}

function checkField(key, settings, savedSettings) {
  if (!savedSettings[key] && !settings[key]) return false;
  return savedSettings[key] !== settings[key];
}

function WebsitePreferencesPageChild({ settingsRecord }) {
  const FIELDS = [
    // "name",  // read-only
    "title",
    "description",
    "domain",
    "domain_redirect",
    "google_analytics",
    "google_tag_manager",
    "password",
    "password_enabled",
    "password_message"
  ];
  let settingsData = {};
  try {
    settingsData = JSON.parse(settingsRecord.json);
  } catch (e) {
    // pass
  }

  const [updateSettings] = useMutation(UPDATE_SETTINGS);
  const savedSettings = useRef({
    ...settingsData
  });

  const [isDirty, setIsDirty] = useState(false);
  const [settings, setSettings] = useState(savedSettings.current);

  const handleDiscard = useCallback(() => {
    setSettings(savedSettings.current);
    setIsDirty(false);
  }, []);
  const handleSave = useCallback(() => {
    updateSettings({
      variables: { id: settingsRecord.id, json: JSON.stringify(settings) }
    });
    savedSettings.current = settings;
    setIsDirty(false);
  }, [settings]);

  const handleFieldChange = useCallback(
    (field, value) => {
      const newSettings = { ...settings };
      newSettings[field] = value;
      const isDirty = FIELDS.some(key =>
        checkField(key, newSettings, savedSettings.current)
      );
      setSettings(newSettings);
      setIsDirty(isDirty);
    },
    [settings]
  );

  const contextualSaveBarMarkup = isDirty ? (
    <ContextualSaveBar
      message="Unsaved changes"
      saveAction={{
        onAction: handleSave
      }}
      discardAction={{
        onAction: handleDiscard
      }}
    />
  ) : null;

  return (
    <Page title="Preferences">
      {contextualSaveBarMarkup}
      <Layout>
        <Layout.AnnotatedSection
          title="SEO & Sharing"
          description="The title and meta description help define how your site shows up on search engines."
        >
          <Card sectioned>
            <FormLayout>
              <TextField
                label="Homepage title"
                maxLength={70}
                showCharacterCount
                value={settings.title}
                onChange={value => handleFieldChange("title", value)}
              />
              <TextField
                label="Homepage meta description"
                maxLength={320}
                showCharacterCount
                multiline={4}
                value={settings.description}
                onChange={value => handleFieldChange("description", value)}
              />
            </FormLayout>
          </Card>
        </Layout.AnnotatedSection>

        <Layout.AnnotatedSection
          title="Google Analytics"
          description="Google Analytics enables you to track the visitors to your site and generates reports that will help you with your marketing."
        >
          <Card sectioned>
            <FormLayout>
              <TextField
                label="Google Analytics account"
                placeholder="UA-xxxxxxxxx-x"
                value={settings.google_analytics}
                onChange={value => handleFieldChange("google_analytics", value)}
              />
            </FormLayout>
          </Card>
        </Layout.AnnotatedSection>
        <Layout.AnnotatedSection
          title="Facebook Pixel"
          description="Facebook Pixel helps you create ad campaigns to find new customers that look most like your customers."
        >
          <Card sectioned>
            <FormLayout>
              <TextField
                label="Facebook Pixel ID"
                placeholder="123456789012345"
                value={settings.facebook_pixel}
                onChange={value => handleFieldChange("facebook_pixel", value)}
              />
            </FormLayout>
          </Card>
        </Layout.AnnotatedSection>
        <Layout.AnnotatedSection
          title="Google Tag Manager"
          description="Google Tag Manager lets you dynamically add additional tags to your page, such as Facebook pixel etc."
        >
          <Card sectioned>
            <FormLayout>
              <TextField
                label="Google Tag Manager account"
                placeholder="GTM-xxxxxxx"
                value={settings.google_tag_manager}
                onChange={value =>
                  handleFieldChange("google_tag_manager", value)
                }
              />
            </FormLayout>
          </Card>
        </Layout.AnnotatedSection>
        <Layout.AnnotatedSection
          title="Password protection"
          description="Enable the password to restrict access to your site. Only customers with the password can access it."
        >
          <Card sectioned>
            <FormLayout>
              <Checkbox
                label="Enable password"
                checked={settings.password_enabled}
                onChange={value => handleFieldChange("password_enabled", value)}
              />
              <TextField
                label="Password"
                value={settings.password}
                onChange={value => handleFieldChange("password", value)}
              />
              <TextField
                label="Message to your visitors"
                maxLength={2000}
                showCharacterCount
                multiline={4}
                value={settings.password_message}
                onChange={value => handleFieldChange("password_message", value)}
              />
            </FormLayout>
          </Card>
        </Layout.AnnotatedSection>
        <Layout.Section>
          <PageActions
            primaryAction={{
              content: "Save",
              onAction: handleSave,
              disabled: !isDirty
            }}
          />
        </Layout.Section>
      </Layout>
    </Page>
  );
}
