import React, { useCallback, useState } from "react";
import {
  Card,
  Page,
  DatePicker,
  Button,
  Popover,
  FormLayout,
  Select,
  TextField,
  Layout,
  TextStyle,
  Icon,
  Stack,
  DisplayText,
} from "@shopify/polaris";
import {
  CalendarMinor,
  ArrowUpMinor,
} from '@shopify/polaris-icons';


function RangeDatePicker() {
  const now = new Date();
  const [{ month, year }, setDate] = useState({
    month: now.getMonth()-1,
    year: now.getFullYear(),
  });
  const [selectedDates, setSelectedDates] = useState({
    start: new Date(),
    end: new Date()
  });

  const handleMonthChange = useCallback((month, year) => setDate({ month, year }), []);

  return <DatePicker
    month={month}
    year={year}
    onChange={setSelectedDates}
    onMonthChange={handleMonthChange}
    selected={selectedDates}
    multiMonth={true}
    allowRange={true}
  />;
}

function PopoverForm() {
  const [popoverActive, setPopoverActive] = useState(false);
  const [tagValue, setTagValue] = useState('');

  const togglePopoverActive = useCallback(
    () => setPopoverActive((popoverActive) => !popoverActive),
    [],
  );

  const handleTagValueChange = useCallback((value) => setTagValue(value), []);

  const activator = (
    <Stack alignment="center">
      <Button onClick={togglePopoverActive} icon={<Icon source={CalendarMinor}/>}>
        Last week
      </Button>
      <TextStyle variation="subdued">compared to {'previous week'}</TextStyle>
    </Stack>
  );

  return (
    <div style={{ maxWidth: '70%', maxHeight: 300 }}>
    <Popover
      active={popoverActive}
      activator={activator}
      onClose={togglePopoverActive}
      // ariaHaspopup={false}
      sectioned
      preferredAlignment="left"
      fullWidth
      fullHeight
    >
      <FormLayout>
        <Select label="Show all customers where:" options={['Tagged with']} />
        <TextField
          label="Tags"
          value={tagValue}
          onChange={handleTagValueChange}
        />
        <Button size="slim">Add filter</Button>
        <RangeDatePicker/>
      </FormLayout>
    </Popover>
    </div>
  );
}

