// @ts-nocheck
import React, { useState, useCallback, useRef } from "react";
import {
  Page,
  Layout,
  Card,
  FormLayout,
  TextField,
  Badge,
  Thumbnail,
  Avatar,
  PageActions,
  ResourceList,
  TextStyle,
  Stack,
  TextContainer,
  DisplayText,
  MediaCard,
  VideoThumbnail,
  Checkbox,
  ContextualSaveBar,
  Loading,
  SkeletonPage,
  SkeletonDisplayText,
  SkeletonBodyText,
  ResourceItem,
} from "@shopify/polaris";
import { InviteMinor } from "@shopify/polaris-icons";
import { gql } from "apollo-boost";
import { useQuery, useMutation } from "@apollo/react-hooks";

import { UserAvatar, BillingAvatar } from '../lib/imageUtils';

const QUERY_SETTINGS = gql`
  {
    settingsItems(filter: { category: { eq: "website" } }) {
      edges {
        node {
          id
          json
        }
      }
    }
  }
`;

const UPDATE_SETTINGS = gql`
  mutation UpdateSettings($id: ID!, $json: String!) {
    updateOneSettingsItem(input: { id: $id, update: { json: $json } }) {
      id
      json
    }
  }
`;

export default function SettingsBillingPage() {
  const { loading, error, data } = useQuery(QUERY_SETTINGS);

  if (loading)
    return (
      <SkeletonPage title="Preferences">
        <Loading />
        <Layout>
          <Layout.Section>
            <Card sectioned>
              <TextContainer>
                <SkeletonDisplayText size="small" />
                <SkeletonBodyText lines={9} />
              </TextContainer>
            </Card>
          </Layout.Section>
        </Layout>
      </SkeletonPage>
    );
  // TODO - removed so we can have a preview of the page
  // if (error)
  //   return (
  //     <Page title="Preferences">
  //       <Layout>
  //         <Layout.Section>
  //           <p>Ops, something went wrong. Refresh to try again.</p>
  //         </Layout.Section>
  //       </Layout>
  //     </Page>
  //   );

  const settingsRecord = data && data.settingsItems.edges.length
    ? data.settingsItems.edges[0].node
    : {};
  return <SettingsBillingPageChild settingsRecord={settingsRecord} />;
}

function checkField(key, settings, savedSettings) {
  if (!savedSettings[key] && !settings[key]) return false;
  return savedSettings[key] !== settings[key];
}

function renderItem(item) {
  const {
    id,
    name,
    created,
    status,
    total,
  } = item;

  return (
    <ResourceItem
      id={id}
      url={`/settings/bills/${id}`}
    >
      <div style={{ display: 'flex', justifyContent: 'space-between' }}>
        <div style={{ width: '30%' }}>
          <h3>
            <TextStyle variation="strong">{name}</TextStyle>
          </h3>
        </div>

        <div style={{ width: '40%' }}>
          <div>Created: {created}</div>
        </div>

        <div style={{ width: '20%' }}>
          <Badge>{status}</Badge>
        </div>

        <div style={{ width: '10%' }}>
          {total}
        </div>
      </div>
    </ResourceItem>
  );
}

function SettingsBillingPageChild({ settingsRecord }) {
  const FIELDS = [
    // "name",  // read-only
    "title",
    "description",
    "domain",
    "domain_redirect",
    "google_analytics",
    "google_tag_manager",
    "password",
    "password_enabled",
    "password_message"
  ];
  let settingsData = {};
  try {
    settingsData = JSON.parse(settingsRecord.json);
  } catch (e) {
    // pass
  }

  const [updateSettings] = useMutation(UPDATE_SETTINGS);
  const savedSettings = useRef({
    ...settingsData
  });

  const [isDirty, setIsDirty] = useState(false);
  const [settings, setSettings] = useState(savedSettings.current);

  const handleDiscard = useCallback(() => {
    setSettings(savedSettings.current);
    setIsDirty(false);
  }, []);
  const handleSave = useCallback(() => {
    updateSettings({
      variables: { id: settingsRecord.id, json: JSON.stringify(settings) }
    });
    savedSettings.current = settings;
    setIsDirty(false);
  }, [settings]);

  const handleFieldChange = useCallback(
    (field, value) => {
      const newSettings = { ...settings };
      newSettings[field] = value;
      const isDirty = FIELDS.some(key =>
        checkField(key, newSettings, savedSettings.current)
      );
      setSettings(newSettings);
      setIsDirty(isDirty);
    },
    [settings]
  );

  const contextualSaveBarMarkup = isDirty ? (
    <ContextualSaveBar
      message="Unsaved changes"
      saveAction={{
        onAction: handleSave
      }}
      discardAction={{
        onAction: handleDiscard
      }}
    />
  ) : null;

  return (
    <Page
      title={"Billing"}
      breadcrumbs={[{ content: "Settings", url: "/settings" }]}
    >
      {contextualSaveBarMarkup}
      <Layout>
        <Layout.AnnotatedSection
          title="Bills"
          description={<p>Bills include your subscription, app charges and transaction fees, if any.<br/><br/>Monthly bills are on a 30-day cycle.</p>}
        >
          <Card title="Current billing cycle: Jun 6, 2020-Jul 6, 2020" sectioned>
            <Card.Section>
              <Card.Subsection>
                <Stack>
                  <Stack.Item fill>Subscription</Stack.Item>
                  <Stack.Item>$299.00</Stack.Item>
                </Stack>
              </Card.Subsection>
              <Card.Subsection>
                <strong>
                <Stack>
                  <Stack.Item fill>Running total</Stack.Item>
                  <Stack.Item>$299.00</Stack.Item>
                </Stack>
                </strong>
              </Card.Subsection>
            </Card.Section>
          </Card>
          <Card title="Bills">
            <br/>
            <ResourceList
              items={[
                {
                  id: 101,
                  name: 'Billing cycle',
                  created: 'Jun 6, 2020',
                  status: 'paid',
                  total: '$299.00',
                },
                {
                  id: 108,
                  name: 'Billing cycle',
                  created: 'May 7, 2020',
                  status: 'paid',
                  total: '$299.00',
                },
                {
                  id: 109,
                  name: 'Billing cycle',
                  created: 'Apr 7, 2020',
                  status: 'paid',
                  total: '$299.00',
                },
              ]}
              renderItem={renderItem}
            />
          </Card>
        </Layout.AnnotatedSection>
      </Layout>
    </Page>
  );
}
